html, body
{
    max-height: 100%;
    overflow-x: hidden;
    overflow-y: hidden;
}

body{
  background: rgb(132,206,235);
  background: radial-gradient(circle, rgba(132,206,235,1) 0%, rgba(173,176,236,1) 45%, rgba(255,166,237,1) 75%, rgba(193,200,228,1) 100%);
}


  .responsive__heading{
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 50px 0px;
  }


#main {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;   
}

@media only screen and (max-width: 600px) {
  .lastname{
    animation-duration: 1s;
    animation-name: slidein2;
  }
  
  .firstname{
    animation-duration: 1s;
    animation-name: slidein;
  }
  .myWork_content{
    left: 15vw;
    width: 70vw;
  }
  .aboutMe_content{
    width: 70vw !important;
    left: 15vw !important;
  }

  .responsive__project{
    width: 175px !important;
    height: 140px !important;
  }
  .responsive__project h1{
    font-size: 1.15em !important;
    margin-bottom: 0.15em;
  }
  .responsive__project p{
    margin: auto 5px;
  }
  .responsive__technologies{
    width: 70vw !important;
  }
}

@media only screen and (min-width: 601px) {
  .lastname{
    transform: scale(1.5);
    animation-duration: 1s;
    animation-name: slidein2;
  }

  .firstname{
    transform: scale(1.5);
    animation-duration: 1s;
    animation-name: slidein;
    margin-bottom: 50px;
  }
  .myWork_content{
    left: 12.5vw;
    width: 75vw;
    
  }
  .responsive__project h1{
    margin-top: .35em;
  }
  .responsive__project p{
    margin: auto 15px;
  }
}

.fall{
  transform: translateY(100vh);
  transition: transform 4s;
}


.return{
  transform: translateY(-100vh);
  animation-duration: 3s;
  animation-name: return;
}

@keyframes slidein {
  from {
    margin-left: -50vw;
  }

  to {
    margin-left: 0vw;
  }
}

@keyframes slidein2 {
  from {
    margin-right: -50vw;
  }

  to {
    margin-right: 0vw;
  }
}

@keyframes return {
  to {
    transform: translateY(0vh);
  }
}

.MyWork{
  transform: rotate(-90deg) scale(1.5);
  animation-duration: 4s;
  animation-name: slidedown;
  transition: margin-top 0.5s;
  z-index: 5;
}

.MyWork:hover > g{
fill: white;
}

.MyWork:hover{
  cursor: pointer;
  margin-top: -30px;
  }

.AboutMe{
  transform: rotate(90deg) scale(1.5);
  animation-duration: 3s;
  animation-name: slideup;
  transition: margin-top 0.5s;
  z-index: 5;
}


.AboutMe:hover > g{
  fill: white;
  }

  .AboutMe:hover{
    cursor: pointer;
    margin-top: -30px;
    }

@keyframes slidedown {
  0% {
    margin-bottom: -200vh;
  }

  50% {
    margin-bottom: -200vh;
  }

  100% {
    margin-bottom: 0vh;
  }
}

@keyframes slideup {
  0% {
    margin-top: -200vh
  }

  50% {
    margin-top: -200vh
  }

  100% {
    margin-top: 0vh;
  }
}

.myWork_content{
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  top: 200%;
}

@media only screen and (max-width: 719px){
.responsive__technologies{
  justify-content: flex-start !important;
}

.responsive__technologies ul{
margin-top: 3px;
margin-bottom: 0;
}

}

@media only screen and (max-width: 746px) and (min-width: 601px){
  .myWork_content{
    overflow-y: scroll;
   justify-content: flex-start;
  }
}

.aboutMe_content{
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50vw;
  height: 100vh;
  left: 25vw;
  top: 200%;
}

.myWork_content-enter{
  top: -200%; 
}
.myWork_content-enter-active {
  top: 0%;
  transition: all 1250ms;
}
.myWork_content-enter-done {
  top: 0%;
}

.myWork_content-exit{
  top: 0%;
}
.myWork_content-exit-active {
  top: 200%;
  transition: all 1250ms;
}
.myWork_content-exit-done {
  top: 200%;
}


.homeExit-exit-active {
  transform: translateY(300vh) !important;
  transition: all 2000ms;
}

.homeExit-exit-done{
  transform: translateY(-300vh) !important;
}

.homeExit-enter {
transform: translateY(-300vh) !important;
}

.homeExit-enter-active {
  transform: translateY(0vh) !important;
  transition: all 2000ms;
}

.homeExit-enter-done{
  transform: translateY(0vh) !important;
}

.responsive__project{
  transition: all 0.5s;
}

.projectcolor:hover{
  transform: translateY(-5px);
  cursor: pointer;
  background-color:#eba484 !important;
}

.projectcolor2:hover{
  transform: translateY(-5px);
  cursor: pointer;
  background-color:#e668cd !important;
}

.email:hover{
  cursor: pointer;
  color: #e6688e !important;
}

.email{
  animation-duration: 5s;
  animation-name: slidedown2;
  transition: all 0.5s;
}

.phone{
  animation-duration: 5s;
  animation-name: slideup2;
  transition: all 0.5s;
}

@keyframes slidedown2 {
  0% {
    transform: translateY(-100vh);
  }

  50% {
    transform: translateY(-100vh);
  }

  100% {
    transform: translateY(0vh);
  }
}

@keyframes slideup2 {
  0% {
    transform: translateY(100vh);
  }

  50% {
    transform: translateY(100vh);
  }

  100% {
    transform: translateY(0vh);
  }
}

@media only screen and (max-width: 400px) and (max-height: 700px){
.aboutMe_content{
  font-size: 0.75em !important;
}

.responsive__project{
  width: 125px !important;
  height: 100px !important;
}
.responsive__project h1{
  font-size: 1em !important;
  margin-bottom: 0.15em;
}
.responsive__project p{
  font-size: 0.65em !important;
  margin: auto 5px;
}

}